<template>
    <div>
        <h3> Roll New Character </h3>
        <input type="text" class="form-control" id="textName" v-model="name"/>
        <div class="form-check">
            <input type="checkbox" class="form-check-input" id="checkDied" v-model="died" />
            <label class="form-check-label" for="checkDied">Did you die?</label>
        </div>
        <div class="form-check">
            <input type="checkbox" class="form-check-input" id="checkProfession" v-model="keepProfession" />
            <label class="form-check-label" for="checkProfession">Do you want to keep your profession?</label>
        </div>
        <button type="submit" class="btn btn-primary" @click="rollNewCharacter">Submit</button>
    </div>
</template>

<script>
    import { mapState, mapMutations } from 'vuex'
    import * as lib from "../lib/lib.js"

    export default {
        name: "RollNewCharacter",

        data: function () {
            return {
                died: false,
                keepProfession: true,
                name: "",
            }
        },

        methods: {
            ...mapMutations(['setCharacter']),
            rollNewCharacter: function () {
                if (this.validated) {
                    this.setCharacter(lib.rollNewCharacter( this.name
                                                          , this.character
                                                          , this.died
                                                          , this.keepProfession
                                                          , undefined));

                    this.died = false;
                    this.keepProfession = true;
                    this.name = "";
                }
            },
        },

        computed: {
            ...mapState(['character']),
            validated: function () {
                return this.name.length > 0;
            },
        },
    }
</script>

