<template>
    <div>
        <b-card-group >
            <b-card border-variant="primary"
                    header="Str"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">

                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrStr">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.str }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpStr">+</button>
                    </div>
                    ({{ strBonus }})
                </b-card-text>
            </b-card>
            <b-card border-variant="primary"
                    header="Dex"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">
                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrDex">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.dex }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpDex">+</button>
                    </div>
                    ({{ dexBonus }})
                </b-card-text>
            </b-card>
            <b-card border-variant="primary"
                    header="Con"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">
                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrCon">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.con }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpCon">+</button>
                    </div>
                    ({{ conBonus }})
                </b-card-text>
            </b-card>

            <b-card border-variant="primary"
                    header="Int"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">
                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrInt">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.int }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpInt">+</button>
                    </div>
                    ({{ intBonus }})
                </b-card-text>
            </b-card>
            <b-card border-variant="primary"
                    header="Wis"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">
                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrWis">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.wis }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpWis">+</button>
                    </div>
                    ({{ wisBonus }})
                </b-card-text>
            </b-card>
            <b-card border-variant="primary"
                    header="Cha"
                    header-bg-variant="primary"
                    header-text-variant="white"
                    align="center">
                <b-card-text>
                    <div class="row">
                        <button class="btn btn-secondary btn-block col-4" @click="decrCha">-</button>
                        <div class="col-4">
                            <b>{{ character.stats.cha }}</b>
                        </div>
                        <button class="btn btn-secondary btn-block col-4" @click="bumpCha">+</button>
                    </div>
                    ({{ chaBonus }})
                </b-card-text>
            </b-card>
        </b-card-group>
    </div>
</template>

<script>
    import { mapState, mapGetters, mapMutations } from 'vuex'

    export default {
        methods: {
            ...mapMutations(['setCharacter']),
            bumpStr: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        str: this.character.stats.str + 1,
                    }
                })
            },
            decrStr: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        str: this.character.stats.str - 1,
                    }
                })
            },

            bumpDex: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        dex: this.character.stats.dex + 1,
                    }
                })
            },
            decrDex: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        dex: this.character.stats.dex - 1,
                    }
                })
            },

            bumpCon: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        con: this.character.stats.con + 1,
                    }
                })
            },
            decrCon: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        con: this.character.stats.con - 1,
                    }
                })
            },

            bumpInt: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        int: this.character.stats.int + 1,
                    }
                })
            },
            decrInt: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        int: this.character.stats.int - 1,
                    }
                })
            },

            bumpWis: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        wis: this.character.stats.wis + 1,
                    }
                })
            },
            decrWis: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        wis: this.character.stats.wis - 1,
                    }
                })
            },

            bumpCha: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        cha: this.character.stats.cha + 1,
                    }
                })
            },
            decrCha: function () {
                this.setCharacter({
                    ...this.character,
                    stats: {
                        ...this.character.stats,
                        cha: this.character.stats.cha - 1,
                    }
                })
            },
        },
        computed: {
            ...mapState(['character']),
            ...mapGetters(['strBonus', 'dexBonus', 'conBonus', 'intBonus', 'wisBonus', 'chaBonus']),
        }
    }
</script>

<style scoped>
